<template>
  <v-row justify="center">
    <v-dialog v-model="show" max-width="500px" >
      <v-card>
        <v-card-title>
          <span class="headline">{{ title }}</span>
        </v-card-title>
        <v-card-text>
          <v-tabs
            v-model="tab"
            fixed-tabs
          >
            <v-tab key="general">General</v-tab>
            <v-tab key="permissions" v-if="!item.permissions.superuser">Permissions</v-tab>
            <v-tabs-items v-model="tab">
              <v-tab-item key="general">
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="12" md="6">
                      <v-text-field 
                        v-model="item.username" 
                        label="Username*"
                        autofocus
                      >
                      </v-text-field>
                    </v-col>
                    <v-col cols="12" sm="12" md="6">
                      <v-text-field 
                        v-model="item.login" 
                        label="Login*">
                      </v-text-field>
                    </v-col>
                    <v-col cols="12" sm="12" md="6">
                      <v-text-field 
                        v-model="item.password" 
                        label="Password" 
                        type="password">
                      </v-text-field>
                    </v-col>
                    <v-col cols="12" sm="12" md="6">
                      <v-text-field 
                        v-model="item.password2" 
                        label="Confirm password" 
                        type="password">
                      </v-text-field>
                    </v-col>
                    <v-col cols="12" sm="12" md="6">
                      <v-switch 
                      v-model="item.permissions.superuser" 
                      label="Superuser">
                    </v-switch>
                    </v-col>
                  </v-row>
                </v-container>
                <small>*indicates required field</small>
              </v-tab-item>
              <v-tab-item key="permissions">
                <div>
                  <v-select
                    :items="permissions"
                    label="Devices"
                    v-model="item.permissions.devices"
                  ></v-select>
                </div>
                <div>
                  <v-select
                    :items="permissions"
                    label="Reports"
                    v-model="item.permissions.reports"
                  ></v-select>
                </div>
              </v-tab-item>
            </v-tabs-items>
          </v-tabs>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="show = false">Cancel</v-btn>
          <v-btn color="blue darken-1"  text @click="save">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
  export default {
    name: 'UserItem',
    data: function(){
      return {
        tab: 0,
        permissions: [
          'none',
          'read',
          'write'
        ],
      }
    },
    props: {
      value: Boolean,
      item: Object,
      title: String,
      save: Function,
    },
    computed: {
      show: {
        get () {
          return this.value
        },
        set (value) {
          this.$emit('input', value)
        }
      }
    }
  }
</script>