<template>
  <div>
    <v-card>
      <v-subheader>
        Users
        <v-spacer></v-spacer>
        <v-btn 
          icon
          v-if="permission=='write'"
        >
          <v-btn
            color="pink"
            dark
            fab
            fixed
            small
            @click="addItem"
          >
            <v-icon>mdi-plus</v-icon>
          </v-btn>
        </v-btn>
      </v-subheader>
      <v-card-title>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Filter"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="users"
        :search="search"
        sort-by="username"
      >
        <template v-slot:item.superuser="{ item }">
          <v-simple-checkbox v-model="item.permissions.superuser" disabled></v-simple-checkbox>
        </template>
        <template v-slot:item.actions="{ item }"  v-if="permission=='write'">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            mdi-pencil
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
    </v-card>
    <template>
      <user-item 
        v-model="item_dialog"
        :item="item"
        :title="item._id=='' ? 'New user' : 'Edit user'"
        :save="saveItem"
        :devices="devices"
      >
      </user-item>
    </template>
    <template>
      <dialog-confirm 
        v-model="confirm_dialog"
        title="Are you sure?"
        :message="confirm_message"
        :confirm="confirmDeleteItem"
      >
      </dialog-confirm>
    </template>
    <template>
      <dialog-alert 
        v-model="alert_dialog"
        :message="alert_message"
      >
      </dialog-alert>
    </template>
  </div>
</template>

<script>
  import DialogAlert from '../components/Dialog.alert.vue'
  import DialogConfirm from '../components/Dialog.confirm.vue'
  import UserItem from '../components/Users.item.vue'

  export default {
    data () {
      return {
        search: '',
        headers: [
          {
            text: 'Username',
            align: 'start',
            value: 'username',
          },
          { text: 'Login', value: 'login' },
          { text: 'Superuser', value: 'superuser', filterable: false, sortable: false },
          { text: 'Actions', value: 'actions', filterable: false, sortable: false },
        ],
        users: [],
        item: {
          permissions: {
          }
        },
        empty_item: {
          _id: '',
          username: '',
          login: '',
          permissions: {
            superuser: false,
            devices: 'none',
            users: 'none',
            reports: 'none'
          }
        },
        deleteIndex: -1,
        item_dialog: false,
        confirm_dialog: false,
        confirm_message: '',
        alert_dialog: false,
        alert_message: '',
      }
    },
    methods: {
      getList () {
        this.$http.get('backend/users')
        .then((response) => {
          if (response['status'] == 200) {
            this.users = response.data['users'] || {}
          }
        })
        .catch((error) => {
          console.log(error);
        })
      },
      addItem () {
        // clear item object
        this.item = JSON.parse(JSON.stringify(this.empty_item));
        this.item_dialog = true
      },
      editItem (item) {
        this.$http.get('backend/user/' + item['_id'])
        .then((response) => {
          if (response['status'] == 200) {
            if (response.data['status']== 'success'){
              this.item = Object.assign(this.item, this.empty_item, response.data['data'])
              this.item_dialog = true
            } else {
              this.alert_dialog_open(response.data['error'])
            }
          }
        })
        .catch((error) => {
          console.log(error);
          this.alert_dialog_open('something is wrong!')
        })
      },
      deleteItem (item) {
        this.deletedIndex = this.users.indexOf(item)
        this.confirm_message = 'Delete this user?<br/> You will not be able to recover it'
        this.confirm_dialog = true
      },
      saveItem () {
        if (this.check_input_data()) {
          let user_data = {}
          if (this.item.password == '' || this.item.password == undefined) {
            user_data = {
              'username': this.item.username,
              'login': this.item.login,
              'permissions': this.item.permissions
            }
          } else {
            let crypto = require('crypto');
            let sha256 = crypto.createHash("sha256");
            sha256.update(this.item.password);
            user_data = {
              'username': this.item.username,
              'login': this.item.login,
              'password': sha256.digest("hex"),
              'permissions': this.item.permissions
            }
          }
          if (this.item._id === '') {
            // new user
            this.$http.post('backend/user/', user_data)
            .then((response) => {
              if (response['status'] == 200) {
                if (response.data['status'] == 'success') {
                  this.users.push({
                    _id: response.data.data,
                    username: user_data.username,
                    login: user_data.login,
                    permissions: user_data.permissions,
                    password: '',
                  })
                  this.item_dialog = false
                }
              } else if (response.data['status'] == 'error') {
                this.alert_dialog_open(response.data['error'])
              }
            })
            .catch((error) => {
              console.log(error);
              this.alert_dialog_open('something is wrong!')
            })
          } else {
            // existed user
            this.$http.put('backend/user/'+ this.item._id, user_data)
            .then((response) => {
              if (response['status'] == 200) {
                if (response.data['status'] == 'success') {
                  this.users.every( (item) => {
                    if (item._id == this.item._id) {
                      item = Object.assign(item, this.item)
                      return false
                    }
                    return true
                  })
                } else if (response.data['status'] == 'error') {
                  this.alert_dialog_open(response.data['error'])
                }
              }
              this.item_dialog = false
            })
            .catch((error) => {
              console.log(error);
              this.alert_dialog_open('something is wrong!')
            })
          }
        }
      },
      check_input_data () {
        if (this.item.username == '') {
          this.alert_dialog_open('Username is required')
          return false
        }
        if (this.item.login == '') {
          this.alert_dialog_open('Login is required!')
          return false
        }
        for (let index in this.users) {
          if (this.users[index]._id != this.item._id){
            if (this.users[index].username == this.item.username) {
              this.alert_dialog_open('Duplicated username!')
              return false
            }
            if (this.users[index].login == this.item.login) {
              this.alert_dialog_open('Duplicated login!')
              return false
            }
          }
        }
        if (this.item.password == '' && this.item._id == '') {
          this.alert_dialog_open('Password is required!')
          return false
        }
        if (this.item.password != '' && this.item.password != this.item.password2) {
          this.alert_dialog_open('Passwords are different!')
          return false
        }
        return true
      },
      alert_dialog_open(alert_message) {
        this.alert_dialog = true
        this.alert_message = alert_message
      },
      confirmDeleteItem() {
          this.$http.delete('backend/user/' + this.users[this.deletedIndex]['_id'])
          .then((response) => {
            if (response['status'] == 200) {
              if (response.data['status'] == 'success') {
                this.users.splice(this.deletedIndex)
              }
            }
          })
          .catch((error) => {
            console.log(error);
          })
        this.confirm_dialog = false
      },
    },
    computed: {
      permission () {
        if (this.$store.getters.permissions['superuser']) {
          return 'write'
        } else {
          return this.$store.getters.permissions['users']
        }
      }
    },
    created: function() {
      this.getList();
    },
    components: {
      DialogConfirm,
      DialogAlert,
      UserItem,
    }
  }
</script>
